:root {
  --background-color: #002248;
  --body-background: #004799;
}

.dark {
  color: var(--background-color);
}

* {
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
}

@keyframes site {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.site {
  visibility: hidden;
  background-image: linear-gradient(white, var(--background-color));
  opacity: 0;
  animation: site 2s forwards;
  animation-delay: 5s;
}

@keyframes welcome-disappear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.welcome {
  background-color: var(--background-color);
  min-height: 100vh;
  height: 100%;
  max-width: 100vw;
  font-size: 100%;
  animation: welcome-disappear 1.5s ease-in-out forwards;
  animation-delay: 3.5s;
  z-index: 20;
}

@keyframes welcome {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.welcome-header {
  animation: welcome 2s ease-in-out forwards;
}

#back-container {
  display: flex;
  justify-content: flex-end;
}

.map {
  max-width: 800px;
  max-height: 450px;
  width: 80vw;
}

.wrapped-menu {
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
  color: white;
  align-items: center;
  width: 100vw;
  position: absolute;
  top: -250px;
  z-index: 1;
  transition: transform 1s;
}

.mobile-nav {
  justify-content: space-between;
}

.menu-wrapper {
  background-color: transparent;
  color: white;
  border: none;
  display: flex;
  flex-direction: column;
  margin: 7px;
  z-index: 3;
}

.hamburger-line {
  width: 30px;
  height: 4px;
  background-color: white;
  margin: 4px 10px;
  transition: transform 1s;
}

@media screen and (max-width: 320px) {
  html {
    font-size: 5px;
  }
  .mobile-nav {
    display: flex !important;
  }

  .desktop-nav {
    display: none !important;
  }
}

@media screen and (min-width: 320px) and (max-width: 640px) {
  html {
    font-size: 8px;
  }

  .logo {
    width: 150px;
    height: auto;
  }

  .mobile-nav {
    display: flex !important;
  }
  .desktop-nav {
    display: none !important;
  }
}

@media screen and (min-width: 641px) and (max-width: 730px) {
  html {
    font-size: 15px;
  }
  .logo {
    width: 250px;
    height: auto;
  }
  .mobile-nav {
    display: flex !important;
  }
  .desktop-nav {
    display: none !important;
  }
}

@media screen and (min-width: 731px) and (max-width: 960px) {
  html {
    font-size: 15px;
  }
  .logo {
    width: 250px;
    height: auto;
  }
  .mobile-nav {
    display: none;
  }
}

@media screen and (min-width: 961px) {
  html {
    font-size: 17px;
  }
  .mobile-nav {
    display: none;
  }
}

body {
  font-size: 20px;
  font-family: "Source Sans Pro", "sans-serif";
  background-image: linear-gradient(white, var(--background-color));
}

.stitch-container {
  font-size: 100vw;
}

.stitch {
  font-size: 0.14em;
  margin-right: 0.055em;
  color: white;
}

.link {
  text-decoration: none;
  margin: 5px;
  color: white;
  font-size: 2rem;
  z-index: 3;
  border-bottom: 1px solid white;
  height: auto;
}

.link:hover {
  color: yellow;
  transition: color 1.5s;
  border-bottom: 1px solid yellow;
}
.flex {
  display: flex;
}

.center {
  justify-content: center;
  align-items: center;
}

.start {
  align-items: center;
  justify-content: flex-start;
}

.wrap {
  flex-wrap: wrap;
}

.column {
  flex-direction: column;
}

.between {
  justify-content: space-between;
}

.around {
  justify-content: space-around;
}

.baseline {
  align-items: baseline;
}

.margin-top {
  margin-top: 5rem;
}

.nav {
  background-color: var(--background-color);
  z-index: 3;
}

.section {
  min-height: 100vh;
  position: relative;
  max-width: 100vw;
}

.footer {
  background-color: var(--background-color);
}

.icon {
  font-size: 2.5rem;
  color: white;
  padding: 5px;
}

.icon:hover {
  color: yellow;
  transition: color 1s;
}

.sign {
  color: white;
  font-size: 1em;
}

.photo {
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 300px;
  height: 50vh;
  background-size: cover;
}

.offer {
  background-image: url("../assets/offer.jpg");
}

.about {
  background-image: url("../assets/about.jpg");
}

.contact {
  background-image: url("../assets/contact.jpg");
}

.photo-header {
  background-color: var(--background-color);
  height: 100%;
  max-width: 100vw;
  font-size: 10rem;
  color: white;
  padding: 0px;
  margin: 0px;
}

.mask {
  background-color: var(--background-color);
  height: 100%;
  width: 100%;
  opacity: 0.5;
}

.content {
  height: 100%;
  margin-bottom: 5%;
  width: 100%;
  margin: 0px;
  padding: 5%;
  box-sizing: border-box;
}

.offer-text {
  align-self: center;
  color: black;
}

@keyframes underline {
  0% {
    text-decoration: none;
  }
  100% {
    text-decoration: underline;
  }
}

.offer-image {
  width: 100%;
  max-width: 370px;
}

.offer-section {
  display: grid;
  grid-template-columns: 50% 50%;
  border-bottom: 1px solid var(--background-color);
  margin-bottom: 3rem;
  box-sizing: border-box;
  background-color: white;
  width: 60%;
  min-width: 400px;
  padding: 30px;
  border-radius: 20%;
  text-decoration: none;
}

.offer-section:hover {
  transform: scale(1.1);
  transition: transform 1s;
  color: black;
}

.button {
  margin: 10px;
  background-color: transparent;
  color: var(--background-color);
  border: 2px solid var(--background-color);
  padding: 10px;
  font-weight: bold;
  width: 150px;
  height: 50px;
}

.button:hover {
  background-image: none;
  background-color: var(--background-color);
  color: yellow;
  transition: 1s;
  cursor: pointer;
}

.about-section {
  width: 80vw;
  min-height: 50vh;
  align-items: center;
  margin-bottom: 20px;
  margin-right: 30px;
  margin-left: 30px;
  box-sizing: border-box;
  font-size: 2rem;
}

.about-section > * {
  padding-top: 20px;
}

.about-header {
  width: 100%;
  text-align: center;
}

.paragraph {
  text-align: justify;
  border-radius: 10px 10px;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 2rem;
}

.about-paragraph {
  text-align: center;
  padding-bottom: 30px;
}

.about-icon {
  color: var(--background-color);
  font-size: 2.5rem;
}

.contact-icon {
  color: yellow;
  font-size: 2.5rem;
}

.contact-header {
  color: yellow;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 2.5rem;
}

.paragraph-contact {
  font-size: 2rem;
}

.section-text {
  padding-left: 20px;
}

.isopen {
  background-color: yellow;
}

.logos {
  width: 100%;
  justify-content: space-around;
}

.logo-image {
  max-width: 40rem;
  padding-top: 15px;
}

.icon-up {
  color: yellow;
  font-size: 4rem;
  padding: 3px 10px;
  border-radius: 10%;
  position: fixed;
  bottom: 0px;
  border: none;
  display: none;
  z-index: 5;
}

@keyframes opacity-down {
  0% {
    opacity: 0;
    transform: translate(0, -200px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes opacity-left {
  0% {
    opacity: 0;
    transform: translate(200px, 0);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes opacity-right {
  0% {
    opacity: 0;
    transform: translate(-200px, 0);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

#animation-about-1,
#animation-about-2,
#animation-about-3,
#animation-2,
#animation-3,
#animation-4,
#animation-5,
#animation-6,
#animation-7,
#animation-8,
#animation-9 {
  visibility: hidden;
  opacity: 0;
}

.white {
  color: white;
}

.language-icon {
  width: 3rem;
}

